import api from '~api'
import config from '~api-config'

let xjyhList = (params) => api.post('/base/BankCash/list.action',config.api,params);//
let addXjyh= (params) => api.post('/base/BankCash/create.action',config.api,params);//
let editXjyh= (params) => api.post('/base/BankCash/edit.action',config.api,params);//
let updateXjyh= (params) => api.post('/base/BankCash/update.action',config.api,params);//
let deleteXjyh= (params) => api.post('/base/BankCash/delete.action',config.api,params);//

export default {
    xjyhList,
    addXjyh,
    editXjyh,
    updateXjyh,
    deleteXjyh
}



